import { PayloadAction, createSlice } from "@reduxjs/toolkit";
import { ISubjectView } from "../../../common/types/teacherDomainView";

const externalSubjectInitialState: ISubjectView = {
  id: '',
  name: '',
  description: '',
  rules: [],
  teacherId: '',
};

export const externalSubjectSlice = createSlice({
  name: 'externalSubject',
  initialState: externalSubjectInitialState,
  reducers: {
    setExternalSubject: (
      state,
      { payload: externalSubject }: PayloadAction<ISubjectView>
    ) => externalSubject,
    resetExternalSubject: () => ({...externalSubjectInitialState}),
  },
});
